* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #282c34;
  color: white;
}

.container {
  text-align: center;
  max-width: 600px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  padding: 30px;
  border: 1px solid white;
  border-radius: 10px;
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 20px;
}

.footer {
  color: gray;
  margin-top: 30px;
}

.footer a:link {
  color: gray;
}

.footer a:visited {
  color: gray;
}

.forecast {
  margin: 5px;
  padding: 10px;
}
